import React, {Component} from 'react';
import axios from '../../axios';
import { connect } from 'react-redux';
import './ManageNames.css';

class ManageNames extends Component{

    state = {
        name: ''
    };

    changeNameSate = (e) => {
        this.setState({name: e.target.value});
    }

    sendNewName= () => {
        const name = [this.state.name];
        const nameString = this.state.name;
        console.log(name)

        if(this.props.names.includes(nameString) || nameString === ''){
            alert('NO CAN DO HOMIE');
        }else{
            axios.post('/names.json', name)
                .then(response => {
                    if(response.status === 200){
                        const newNames = [...this.props.names];
                        newNames.push(nameString);
                        this.props.updateNames(newNames);
                    }
                })
                .catch(error => console.log(error));
        }

    }


    render(){

        const list = this.props.names.map((name) => {
            return (
                <li>
                    {name}
                </li>
            )
        });

        return(
            <div>
                <h1>Under Construction...</h1>
                <h2>Please enter a new name</h2>
                <input onChange={this.changeNameSate}></input>
                <input
                    type="button"
                    value="Create Name"
                    onClick={this.sendNewName}
                />

                <ul className='nameList'>
                    {list}
                </ul>
            </div>
        )
    }
}


const mapStateToProps = state => {
    return{
        names: state.names
    };
};

const mapDispatchToProps = dispatch => {
    return{
        updateNames: (value) => dispatch({type: 'CHANGE_PAGE', value: value})
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ManageNames);