const initialState = {
    names: [
        'Vic',
        'Vivian',
        'Vicki',
        'Venessa',
        'Forbes',
        'Vincent'
    ],
    pageActive: "/",
    activePages:[
        {
            function:'home',
            label: 'Home',
            path: '/how-long-does-vic-have/'
        },
        {
            function:'countdown',
            label: 'Count Down',
            path: '/how-long-does-vic-have/countdown'
        },
        {
            function:'manageNames',
            label: 'Manage Names',
            path: '/how-long-does-vic-have/manage-names'
        }
    ]
}

const reducer = (state = initialState, action) => {
    switch(action.type){
        case "CHANGE_PAGE":
            return {
                ...state,
                pageActive: action.value
            }
        
        case "UPDATE_NAMES":
            return {
                ...state,
                names: action.value
            }
            

        default:
            return state
    }
}

export default reducer