import React, {Component} from 'react';
import { connect } from 'react-redux';
import './App.css';
import ButtonMenus from "./components/ButtonMenus/ButtonMenus";
import axios from './axios';

class App extends Component{

  componentDidMount = () => {
    axios.get("https://vic-app-df7a3.firebaseio.com/names.json")
        .then(response => {
            const newNames = [...this.props.names];
            const dbNames = response.data;

            for(var n in dbNames){
                const name = dbNames[n][0]
                if(!newNames.includes(name))
                newNames.push(name);
            }
            this.props.updateNames(newNames);
        })
}

  render(){
    const length = this.props.names.length - 1
    const randomNumber = parseInt(Math.random() * length)
    return (
      <div className="App">
        <header className="App-header">
          <h1>Hello...{this.props.names[randomNumber]}</h1>
          <ButtonMenus/>
          
        </header>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return{
    names: state.names
  };
};

const mapDispatchToProps = dispatch => {
  return{
      updateNames: (value) => dispatch({type: 'UPDATE_NAMES', value: value})
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
